  <div class="business-portal-page">
    <portal-header :projectObj="titleConfig"> </portal-header>
    <div class="panel">
      <kyc-form ref="kycForm" mode="edit" :identityId="identityId" :isSelfOrgOper="true" identityClassify="orgOper" />
    </div>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import KycForm from '@/components/kyc-common/kyc-form/index.vue';

export default {
  name: 'ChangeKycinfo',
  components: {
    PortalHeader,
    KycForm,
  },
  data() {
    return {
      identityId: '',
    };
  },
  computed: {
    titleConfig() {
      return {
        title: 'Change KYC',
        desc: 'KYC information of an organization operator. The field marked with * is mandatory.',
      };
    },
  },
  watch: {},
  created() {
    this.identityId = JSON.parse(decodeURIComponent(this.$route.query.data)).identityId;
  },
  methods: {},
};
</script>
